<template>
    <div class="row" v-if="ready">
        <div class="col-12 flex">
            <div class="col-7" v-if="show_form">
                <div> <!--general-->
                    <form id='general_req_form' enctype="multipart/form-data" class="form-horizontal" data-vv-scope="form1"  @submit.prevent="handleSubmit" >
                    <div class="card">
                    <div class="card-header">
                        <strong>Reimbursement Form for Medical Allowance</strong>
                    </div>            
                    <div class="card-body card-block">
                        <div class="row form-group">
                            <div class="col col-md-3"><label class="form-control-label">Name of Applicant</label></div>  <!--cause fresh-->
                            <div class="col-12 col-md-9">
                                <input type="text" class='form-control' v-model='applicant' readonly required/>
                            </div>
                        </div>
                        <div style="display:flex;flex-direction:row">
                            <label style="font-size:0.85em;color:rgba(0,0,0,0.7)">Description of Goods Purchased / Services Delivered:</label>
                        </div>
                        <div class="row form-group" style="margin-left:-1.26em;margin-right:-1.26em">
                        <table class="table table-striped first-td-padding">
                            <thead>
                                <tr>
                                    <th style='width:15%'></th>
                                    <th style='font-size:1.5em!important'>Item 1</th>
                                </tr>
                            </thead>
                            <tbody>
                                <tr>
                                    <td style='width:15%;font-size:1.75em!important;padding-left:1.5em'>Category</td>
                                    <td>
                                        <input type="text" readonly class="form-control" v-model="cat"/>
                                    </td>
                                </tr>
                                <tr>
                                    <td style='width:15%;font-size:1.75em!important;padding-left:1.5em'>Amount HK$</td>
                                    <td v-if="counter>=1">
                                        <input type="number" step=0.01 class="form-control" v-model='amount' :max='fund_max' required/>
                                    </td>
                                </tr>
                                <tr>
                                    <td style='width:15%;font-size:1.75em!important;padding-left:1.5em'>Invoice\Receipt (.pdf)</td>
                                    <td colspan='100%'> 
                                        <input type="file" class="form-control-button" accept="application/pdf" ref="pdf_file" multiple @change="addfile_pdf()" style="height:100%" required/>
                                    </td>
                                </tr>
                                <tr>
                                    <td style='width:15%;font-size:1.75em!important;padding-left:1.5em'>Invoice\Receipt Number</td>
                                    <td colspan='100%'> 
                                        <input v-if="skip_invoice==false" type="text" class="form-control" v-model="invoice" placeholder="Separate multiple invoice with ," :required="skip_invoice==false"/>
                                        <div class='flex'>
                                            <input type='checkbox' value='na' v-model="skip_invoice" style='margin-top:4px;margin-right:8px' @change="makeid(10)"/>
                                            <label style='margin-top:2px'>No invoice number available</label>
                                        </div>                      
                                    </td>
                                </tr>
                                <tr>
                                    <td :class="[{'bottom':signed}]" style='width:15%;font-size:1.75em!important;padding-left:1.5em'>Security Code :</td>
                                    <td colspan='100%' > 
                                        <div v-if="!signed" style="display:flex;flex-direction:row">
                                            <input type="password" v-model='pwd' class="form-control" style='width:30%'/>                   
                                            <button type="button" class="form-control" style='width:20%' ref="submitcode" @click="post_ssc">Sign now !</button>
                                        </div>
                                        <div v-if="signed" style="display:flex;flex-direction:row">
                                            <img class='signature' :src="sign_data"/>
                                        </div>
                                    </td>
                                </tr>
                            </tbody>
                        </table>            
                        </div>
                        <div class="row form-group">
                            <label class="form-control-label" style="margin-left:auto;margin-top:1em;margin-right:1em">
                                <b>Total Amount in HK$: {{ amount }}</b>
                            </label>
                        </div>
                        <div v-html="message">{{message}}</div>
                        </div>     

                    <div class="card-footer">
                        <button type="submit" class="btn btn-primary btn-sm" :disabled="processing">
                            <i class="fa fa-paper-plane"></i> Submit
                        </button>
                        <!--
                        <button type="reset" class="btn btn-danger btn-sm" onclick="document.getElementById('general_req_form').reset()">
                            <i class="fa fa-undo"></i> Reset
                        </button>
                        -->
                    </div>
                </form>
            </div>  
            <!--check form-->  
            </div> 
            <div class="col-5">
                <div class="col-12">
                    <div class="card">
                        <div class="card-header">
                            <strong>Medical Allowance Info.</strong>
                        </div>  
                        <div class="card-body card-block">
                            <div class="col-12 flex" style='margin-bottom:1rem'>             
                                <div class="col-4"></div>
                                <div class="col-4">
                                    <label>Start Period</label>
                                    <input type="month" readonly v-model="st" class="form-control"/>
                                </div>
                                <div class="col-4">
                                    <label>End Period</label>
                                    <input type="month" readonly v-model="ed" class="form-control"/>
                                </div>
                            </div> 
                            <div class="col-12 flex" style='margin-top:1rem;margin-bottom:1rem'>
                                <div class="col-4">
                                    Total Allowance
                                </div>
                                <div class="col-8">
                                    <input type="text" v-model="maxval" class="form-control" readonly/>
                                </div>
                            </div>
                            <div class="col-12 flex" style='margin-top:1rem;margin-bottom:1rem'>
                                <div class="col-4">
                                    Spent
                                </div>
                                <div class="col-8">
                                    <input type="text" v-model="spent" class="form-control" readonly/>
                                </div>
                            </div>
                            <div class="col-12 flex" style='margin-top:1rem;margin-bottom:1rem'>
                                <div class="col-4">
                                    Balance
                                </div>
                                <div class="col-8">
                                    <input type="text" v-model="fund_max" class="form-control" readonly/>
                                </div>
                            </div>
                    </div>
                </div>
            </div>
            <div class="col-12">
                <v-data-table
                    :headers="headers"
                    :items="record_list"
                    class="elevation-1"
                    disable-initial-sort
                    :rows-per-page-items="[10]"               
                    >
                    <template v-slot:items="props">
                        <tr>
                            <td>
                                <u :id="props.item.tag" style="cursor:pointer" @click="open_edit(props.item.id,props.item.tag)">Edit</u><br/>
                                <u :id="props.item.tag" style="cursor:pointer" @click="cancel_reimb(props.item.id)">Cancel</u>
                            </td>
                            <td>{{ props.item.tag }}</td>
                            <td>{{ props.item.amount }}</td>
                            <td>{{ props.item.date_app }}</td>
                            <td><u :id="props.item.tag" style="cursor:pointer" @click="open_reimb(props.item.tag)">View</u></td>
                        </tr>
                        <template v-slot:pageText="props">
                            Items {{ props.pageStart }} - {{ props.pageStop }} out of {{ props.itemsLength }}
                        </template>               
                    </template>
                </v-data-table>  
            </div>
        </div>
        <div v-if="open_editor" class="black_overlay"></div>
        <div v-if="open_editor" class="center_card">     
            <form @submit.prevent="update_med_form">
                <div class="card">
                    <div class="card-header">
                        <strong>Medical Reimbursement : {{ editdata.record_tag }}</strong>
                    </div>
                    <div class="card-body card-block">
                        <table class="table table-striped first-td-padding">
                            <tbody>
                                <tr>
                                    <td class='td_dp'>Date</td>
                                    <td>
                                        <input type='text' class="form-control" v-model="editdata.date" readonly/>
                                    </td>
                                </tr>
                                <tr>
                                    <td class='td_dp'>Amount HK$</td>
                                    <td>
                                        <input type='number' class="form-control" v-model="editdata.amount" :max="editdata.balance" required/>
                                    </td>
                                </tr>
                                <tr>
                                    <td class='td_dp'>Invoice\Receipt (.pdf)</td>
                                    <td colspan='100%'> 
                                        <div v-for="l in editdata.pdflist.length" >
                                            <u><a :href="editdata.pdflist[l-1].pdf_path" target="_blank" class='white_text'>{{ editdata.pdflist[l-1].pdf_name }}</a></u>
                                        </div>
                                        <label for="newpdf" class="form-control cus_btn mb0">Update File</label>
                                        <input id='newpdf' class="hidden_btn" type="file" accept="application/pdf" ref="new_pdf_file" multiple @change="new_addfile_pdf()"/>
                                    </td>
                                </tr>
                                <tr v-if="editdata.changed_pdf">
                                    <td class='td_dp'>Invoice\Receipt Number</td>
                                    <td colspan='100%'> 
                                        <input v-if="editdata.hv_invoice==false" type="text" class="form-control" v-model="editdata.invoice_no" placeholder="Separate multiple invoice with ," :required="skip_invoice==false"/>
                                        <div class='flex'>
                                            <input type='checkbox' value='na' v-model="editdata.hv_invoice" style='margin-top:4px;margin-right:8px' @change="makeid(10)"/>
                                            <label style='margin-top:2px'>No invoice number available</label>
                                        </div>                      
                                    </td>
                                </tr>
                            </tbody>
                        </table>
                    </div>
                    <div class="card-footer">
                        <button type="submit" class="btn btn-primary btn-sm" :disabled="processing">
                            <i class="fa fa-paper-plane"></i> Submit
                        </button>
                        <button type="button" class="btn btn-danger btn-sm" @click="close_overlay">
                            <i class="fa fa-undo"></i> Return
                        </button>
                    </div>
                </div>
            </form>
        </div>
    </div>
</template>


<script>
import axios from 'axios';
export default {
    data(){          
        return{
            editdata:{
                id:'',
                balance:0,
                amount:0,
                date:'',
                pdf:'',
                pdf_path:'',
                pdf_name:'',
                invoice_no:'',
                hv_invoice:false,
                changed_pdf:false,
                record_tag:'',
                pdflist:[],
            },
            open_editor:false,
            original_val:0,
            original_pdf:'',
            new_pdf:false,
            skip_invoice:false,
            processing:false,
            show_form:true,
            record_list:[],
            spent:0.00,
            maxval:0.00,
            st:'',
            ed:'',
            invoice:'',
            check_num:true,
            sum:0,
            cat:'Medical Allowance',
            amount:0,
            pwd:'',
            ready:false,
            sign_data:[],       
            fund_max:0.00,
            counter:1,
            pdf:'',
            level:0,
            message:'',
            user_check:'',
            signed:false,
            headers: [
                { text: 'Action',value:'reimb_tag',sortable: false},
                { text: 'Tag',value:'tag',sortable: false},
                { text: 'Amount',value:'total',sortable: false},
                { text: 'Date',value:'app_date',sortable: true},
                { text: 'Form',value:'reimb_tag',sortable: false},
            ], 
        }  
    },
async created(){
    await axios.get('../permission.php')
    .then(
        (response)=>{
            this.level = response.data[0].proc_level;
        }
    )
    await axios.get('Procurement/admin/backend/med_period.php')
            .then(
                (response)=>{
                    this.st = response.data.st;
                    this.ed = response.data.ed;
                }
            )
    await axios.get('Procurement/shared_backend/check_user_setup.php')
    .then(
        (response)=>{
            this.user_check = response.data[0].signed;
            if(!(this.user_check=='set')){
                this.$router.push('../user_setup');
            }
        }
    )
    await axios.get('../user_info.php')
        .then(
            (response)=>{
                this.applicant = response.data[1];
            }
        ); 
    await this.fetchdata();   
    this.ready = true;        
    //asset category of department   
  },
    async mounted(){
        window.removeEventListener('focus', console.log('Lost Focus'));
        //asset category of department   
    },   
    methods:{
        close_overlay(){
            this.open_editor = false;
        },
        async open_edit(id,tag){
            this.editdata.record_tag = tag;
            this.editdata.id = id;
            this.editdata.changed_pdf = false;
            await this.fetch_med_bal(id);
            await this.fetch_med_data(id);
            this.open_editor = true;
        },
        fetch_med_data(id){
            var formData = new FormData();
            formData.append('rid',id);
            axios.post("Procurement/reimbursement/backend/med_data.php",formData).then(
                (response)=>{
                    this.editdata.date = response.data[0].date_app;
                    this.editdata.amount = response.data[0].amount;
                    this.editdata.pdflist = response.data[0].pdf;
                }
            )
        },
        update_med_form(){
            var formData = new FormData();
            formData.append('st',this.st);
            formData.append('ed',this.ed);
            formData.append('tag',this.editdata.record_tag);
            formData.append('rid',this.editdata.id);
            formData.append('amount',this.editdata.amount);
            formData.append('new_pdf',this.editdata.changed_pdf);
            formData.append('count_files',this.editdata.pdf.length);
            formData.append('invoice',this.editdata.invoice_no);
            for( var i = 0; i < this.editdata.pdf.length; i++ ){
                let file = this.editdata.pdf[i];
                formData.append('files'+i, file);
            }
            axios.post("Procurement/reimbursement/backend/update_med_record.php",formData).then(
                (response)=>{
                    this.$alert("Successfully Updated Medical Reimbursement Record").then(
                        async()=>{
                            await this.fetchdata();  
                            this.open_editor = false;
                        }
                    )             
                }
            )
        },
        async fetchdata(){
            var formData = new FormData();
            formData.append('st',this.st);
            formData.append('ed',this.ed);
            await axios.post('Procurement/reimbursement/backend/med_record.php',formData)
                .then(
                    (response)=>{
                        this.record_list = response.data;
                    }
                )
            await axios.get('Procurement/reimbursement/backend/fetch_med_bal.php')
                .then(
                    (response)=>{
                        this.maxval = response.data[0].max;
                        this.spent = response.data[0].spent;
                        this.fund_max = Number(response.data[0].max)-Number(response.data[0].spent) ;
                    }
                ); 
        },
        fetch_med_bal(id){
            var formData = new FormData();
            formData.append('id',id);
            formData.append('st',this.st);
            formData.append('ed',this.ed);
            formData.append('maxval',this.maxval);
            axios.post('Procurement/reimbursement/backend/med_av_bal.php',formData).then(
                (response)=>{
                    this.editdata.balance = response.data;
                }
            )
        },
        cancel_reimb(id){
            this.$confirm("Confirm cancel reimbursement record ?").then(
                ()=>{
                    var formData = new FormData();
                    formData.append('rid',id);
                    axios.post("Procurement/reimbursement/backend/cancel_med.php",formData).then(
                        (response)=>{
                            this.fetchdata();
                        }
                    )
                }
            )        
        },
        makeid(length) {
            if(this.skip_invoice==true){
                var result = '';
                var characters = 'ABCDEFGHIJKLMNOPQRSTUVWXYZabcdefghijklmnopqrstuvwxyz0123456789';
                var charactersLength = characters.length;
                for ( var i = 0; i < length; i++ ) {
                    result += characters.charAt(Math.floor(Math.random() * charactersLength));
                }
                this.invoice = result;
            } else {
                this.invoice = '';
            }       
            if((this.open_editor==true)&&(this.editdata.hv_invoice==true)){
                var result = '';
                var characters = 'ABCDEFGHIJKLMNOPQRSTUVWXYZabcdefghijklmnopqrstuvwxyz0123456789';
                var charactersLength = characters.length;
                for ( var i = 0; i < length; i++ ) {
                    result += characters.charAt(Math.floor(Math.random() * charactersLength));
                }
                this.editdata.invoice_no = result;
            } else {
                this.editdata.invoice_no = '';
            }    
        },
        addfile_pdf(event){
             this.pdf = this.$refs.pdf_file.files;
        },
        new_addfile_pdf(event){
            this.editdata.pdf = this.$refs.new_pdf_file.files;
            if(this.editdata.pdf!=''){
                this.editdata.changed_pdf = true;
            } else {
                this.editdata.changed_pdf = false;
            }
            console.log(this.editdata.pdf.length);
        },
        async new_list(){
            this.ready=false;
            var formData = new FormData();
            formData.append('st',this.st);
            formData.append('ed',this.ed);
            await axios.post('Procurement/reimbursement/backend/med_record.php',formData)
                .then(
                    (response)=>{
                        this.record_list = response.data;
                    }
                )
            await axios.get('Procurement/reimbursement/backend/fetch_med_bal.php')
                .then(
                    (response)=>{
                        this.maxval = response.data[0].max;
                        this.spent = response.data[0].spent;
                        this.fund_max = Number(response.data[0].max)-Number(response.data[0].spent) ;
                    }
                );  
            this.ready=true; 
        },
        post_ssc(){
            var formData = new FormData();
                formData.append('pwd',this.pwd);
                axios.post('Procurement/reimbursement/backend/check_sign.php',formData)
                .then(
                    (response)=>{
                        if(response.data[0].signature==undefined){
                            this.$alert(response.data);
                            //this.message = response.data;
                        } else {
                        this.sign_data = response.data[0].signature;
                        this.signed=true;
                        this.message='';
                        }
                    }
                )
            this.$forceUpdate();
        },
        open_reimb(tag){
            var url = 'src/Procurement/reimbursement/backend/view_form_app.php?tag='+tag+'&st='+this.st+'&ed='+this.ed;
            window.open(url);
        },
        async handleSubmit(event){
                if(this.signed==true){
                this.processing = true;
                var formData = new FormData();
                var i=0;
                var count_files = 0;
                    formData.append('applicant',this.applicant);
                    formData.append('invoice',this.invoice);
                    formData.append('dept','Medical');
                //counter loop
                formData.append('asset_cat'+i,this.cat);
                formData.append('amount',this.amount);
                for( var i = 0; i < this.pdf.length; i++ ){
                    let file = this.pdf[i];
                    formData.append('files'+i, file);
                    count_files=count_files+1;
                }
                formData.append('count_files', count_files);
                //counter loop
                formData.append('total',this.amount);
                formData.append('r_type','staff');
                formData.append('r_name',this.applicant);
                formData.append('sign_stat',"true");     
                formData.append('counter',this.counter);
                await axios.post('Procurement/reimbursement/backend/insert_reimb_med.php',formData)
                .then(
                    (response)=>{
                        if(response.data=="OK"){
                            this.$alert('Successfully submitted record').then(
                                ()=>{
                                    this.processing = false;   
                                    this.$router.push('my_reimb_records');                               
                                }
                            ) 
                            this.show_form = false;
                            this.new_list();                      
                        } else {
                            this.$alert(response.data);
                        }
                    }
                )
                } else if((this.signed==false)){
                    this.$alert('Please Sign the form !');
                }
        }
    }       
}
</script>


<style scoped>
  .input-group-addon {
    background-color: transparent;
    border-left: 0;
}
input[type=date]::-webkit-inner-spin-button {
    -webkit-appearance: none;
    display: none;
}
.signature{
    border-bottom:1px solid #000000;
    height:5rem;
}
.bottom{
    vertical-align: bottom;
}
.td_dp{
    width:35%;
    font-size:1.75em!important;
    padding-left:1.5em;
    padding-top:0.8em;
}
.cus_btn:hover > .white_text{
    color:#FFFFFF;
}
</style>


